import * as ActionTypes from './ActionTypes';

const INITIAL_STATE = {
    isOpen: false, 
    position: "top",
    person1: {},
    person2: {},
    dialogIdx: 0,
    speakerIdx: 0,
    npcId: null,
    battleConversation: false,
 };

export const Dialog = (state = {...INITIAL_STATE}, action) => {
            switch(action.type) {
                case ActionTypes.START_NEW_JOURNEY:
                    return({
                        ...INITIAL_STATE
                    });
                case ActionTypes.SET_DIALOG_STATUS:
                    return({
                        ...INITIAL_STATE,
                        isOpen: true,
                        position: action.payload.position,
                        person1: action.payload.person1,
                        person2: action.payload.person2,
                        npcId: action.payload.npcId,
                        dialogIdx: 0,
                        speakerIdx: 0,
                        battleConversation: action.payload.battleConversation, 
                    });
                case ActionTypes.RESET_DIALOG_STATUS:
                        return({
                            ...INITIAL_STATE,
                        });
                case ActionTypes.NEXT_DIALOG:
                    if(state.speakerIdx===0) {
                        return({
                            ...state,
                            speakerIdx: 1,
                        })
                    } else {
                        return({
                            ...state,
                            speakerIdx: 0,
                            dialogIdx: (state.dialogIdx + 1) % state.person1.dialogs.length,
                        })
                    }
                default: 
                    return state;
            }
}