const StartBattle = (player, npc) => {
    return({
        type: ActionTypes.START_BATTLE,
        payload: {
            player,
            npc,
        }
    });
} 

const BattleGetRandomMove = (maxMove) => {
    return (Math.floor(Math.random() * (maxMove+1)) % (maxMove+1));
}

const BattleGetPredictedMoveIdx = (arr) => {
    return(arr.reduce((maxIdx, currVal, currIdx, arr) => currVal > arr[maxIdx]? currIdx: maxIdx, 0));
}

const BattleGetWinningMove = (move, maxMove) => {
    switch(move) {
        case ROCK: return PAPER;
        case PAPER: return SCISSORS;
        case SCISSORS: return ROCK;
        default: return BattleGetRandomMove(maxMove);
    }
}

const BattleGetNextMove = (battlerType, markovMatrix, lastMove) => {
    const maxMove = markovMatrix.length - 1;
    let nextMove, predictedMoveIdx;
    if(battlerType===NON_GANG_MEMBER ||  lastMove===null) {
        nextMove = BattleGetRandomMove(maxMove);
    } else {
        predictedMoveIdx = BattleGetPredictedMoveIdx(markovMatrix[lastMove]);
        if(markovMatrix[lastMove][predictedMoveIdx]===0) {
            nextMove = BattleGetRandomMove(maxMove);
        } else {
            nextMove =  BattleGetWinningMove(predictedMoveIdx);
        }
    }
    return nextMove;
}

const BattleGetWinner = (playermove, npcmove) => {
        if(playermove===ROCK) {
            if(npcmove===SCISSORS) 
                return 1; //player won
            else if(npcmove===PAPER)
                return -1; //npc won
            else 
                return 0 //draw
        } else if(playermove===PAPER) {
            if(npcmove===ROCK) 
                return 1; //player won
            else if(npcmove===SCISSORS)
                return -1; //npc won
            else
                return 0 //draw
        } else if(playermove===SCISSORS) {
            if(npcmove===PAPER)
                return 1; //player won
            else if(npcmove===ROCK)
                return -1; //npc won
            else
                return 0 //draw
        }
}

export const BattleMoveIndexToStr = (move) => {
    switch(move) {
        case 0: return "ROCK";
        case 1: return "PAPER";
        case 2: return "SCISSORS";
        default: return "Undefined";
    }
}

const BattleSummary = (playername, playermove, npcname, npcmove, winner) => {
    const playerline = playername + " chose "+ BattleMoveIndexToStr(playermove) + ". ";
    const npcline = npcname+ " chose "+ BattleMoveIndexToStr(npcmove)+". ";
    let winnerline; 
    if(winner===1) {
        winnerline = playername + " wins this round!";
    } else if(winner===-1) {
        winnerline = npcname+ " wins this round!";
    } else {
        winnerline = "Its a DRAW!";
    }
    return(playerline+npcline+winnerline);
}

export const BattleHandleMove = (playerMove) => (dispatch, getState) => {
    
    playSoundEffect(LASER_SOUND);
    let battle = getState().battle;
    const playerMarkovMatrix = battle.player.markovMatrix;
    const playerLastMove = battle.player.lastMove;
    const npcMove = BattleGetNextMove(battle.npc.battlerType, playerMarkovMatrix, playerLastMove);
    const winner = BattleGetWinner(playerMove, npcMove);
    const summary = BattleSummary(battle.player.name, playerMove, battle.npc.name, npcMove, winner);
    dispatch(UpdateBattleStatsAction(playerMove, npcMove, winner, summary));

    const finalWinner = CheckBattleWinner(getState().battle);
    if(finalWinner!==0) {
        battle = getState().battle;
        let newexp = battle.player.exp + getPlayerNewExp(battle.player.score, battle.player.level, battle.npc.level, finalWinner);
        let newlevel = getPlayerLevel(newexp);
        setTimeout( function() { 
            dispatch(EndBattle(finalWinner, {newlevel, newexp, gangMember:finalWinner===1 && battle.npc.battlerType===GANG_MEMBER?battle.npc.name:null}, battle.npc.id));
        }, 1200);
    }
}

const getPlayerNewExp = (score, playerLevel, npcLevel, winner) => {
    const BASE_EXP = 5, LEVEL_MULTIPLIER = 10, WIN_BONUS=10;
    if(winner===1)
        return(BASE_EXP + score + Math.max((npcLevel - (playerLevel-1))*LEVEL_MULTIPLIER, LEVEL_MULTIPLIER) + WIN_BONUS);
    else 
        return(BASE_EXP + score);
}

const getPlayerLevel = (exp) => {
    if(exp < 60) 
        return 1;
    else if(exp < 180)
        return 2;
    else if(exp < 360)
        return 3;
    else if(exp < 540)
        return 4;
    else if(exp < 1000)
        return 5;
    else 
        return 6;
}

const CheckBattleWinner = (battle) => {
    if(battle.player.lives===0) {
        return -1;
    } else if(battle.npc.lives===0) {
        return 1;
    } else {
        return 0;
    }
}

const EndBattle = (battleWinner, updatedPlayerStats, npcId) => {
    return({
        type: ActionTypes.END_BATTLE,
        payload: {
            battleWinner,
            player: updatedPlayerStats,
            npcId,
        }
    });
}

export const CloseBattleSequence = () => (dispatch, getState) => {
    const npc = getNPC(getState().npc.list, getState().battle.npc.id), player = getState().player;
    const VIEWPORT_HEIGHT=getState().viewport.viewportDims[1];
    dispatch(CloseBattle());
    
    if(getState().battle.finalWinner===1) {
        dispatch(SetConversationStatus(npc.id, 
            {name: npc.name, dialogs: NPCDIALOGS[npc.name].battleLose.map(dialog => dialog.replace("$PLAYERNAME", player.name))}, 
            {name: player.name, dialogs: PLAYERDIALOGS[npc.name].battleWin.map(dialog => dialog.replace("$PLAYERNAME", player.name))}, 
            mapToViewport(player.position, getState().viewport.start)[1]>(VIEWPORT_HEIGHT/3)? "top": "bottom", false));
    } else {
        dispatch(SetConversationStatus(npc.id, 
            {name: npc.name, dialogs: NPCDIALOGS[npc.name].battleWin.map(dialog => dialog.replace("$PLAYERNAME", player.name))}, 
            {name: player.name, dialogs: PLAYERDIALOGS[npc.name].battleLose.map(dialog => dialog.replace("$PLAYERNAME", player.name))}, 
            mapToViewport(player.position, getState().viewport.start)[1]>(VIEWPORT_HEIGHT/3)? "top": "bottom", false));
    }
    
} 

